export default {
	data(){
		return{
			btnStr:'获取验证码',
			codeFlag:true
		}
	},
	methods: {
		login(){
			console.log("登录")
		},
		getCodenum(){
			//获取验证码
		console.log("获取验证码")
			if(this.codeFlag){
				//可以获取
				this.codeFlag=false
				let timer=null
				let count=60
				timer=setInterval(()=>{
						this.btnStr=count+'s后重新获取'
						count--
						if(count==0){
							clearInterval(timer)
							this.codeFlag=true
							this.btnStr="获取验证码"
						}
				},1000)
			}else{
				//不可以获取
				uni.showToast({
					title:'正在获取，请稍后！',
					icon:'none'
				})
			}
		}
	}
}
